import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import {useState, useEffect} from 'react'
import {jsonCodeBlock} from '../utils'
import useWebSocket,  { ReadyState } from 'react-use-websocket'
import { Line } from 'react-chartjs-2';
import _ from 'lodash/fp'
import { set } from 'lodash'


let description = (
  <>
    wscat data to the endpoint given below to view live rate information about a service:
        <pre className='code'>
          - message content is not read
          <br/>
          - tee output to this endpoint to monitor message frequency
        </pre>
        <br/>
        (Click within textarea below to copy string)
        <br/>
        <br/>
  </>
)


let MonitorApp = () => {
  let [socketUrl, setSocketUrl] = useState('')

  let loadUrl = async () => {
    try {
      let data = await (await fetch("http://localhost:8080/websocketSession")).json()
      setSocketUrl(`ws://localhost:8081${data.endpoint}`)
    } catch (err) {
      console.log(err.message)
    }
  }

  return (
    <Container>
      {description}
      <button type="submit" onClick={loadUrl}>Start</button>
      <button onClick={() => setSocketUrl('')}>Stop</button>
      {socketUrl !== '' ? <LiveMonitor socketUrl={socketUrl}/> : null}
    </Container>
  )
}

let LiveMonitor = ({socketUrl}) => {
  let [content, setContent] = useState({status: 'standby'})
  let { lastMessage, readyState,  } = useWebSocket(socketUrl.replace('/monitor/', '/stats/'))
  let [rates, setRates] = useState([])
  let [incomingCount, setIncomingCount] = useState(0);

  useEffect(() => {
    if (lastMessage !== null) {
      setContent(
        (prev) => {
          let newMessage = JSON.parse(lastMessage.data)
          setIncomingCount(incomingCount + 1)
          // if ((rates.slice(-5).length == 5) && _.every(_.isEqual(0))(rates.slice(-5))) {
          //   newMessage.status = 'waiting'
          // }
          if (prev && (prev != newMessage) && newMessage.status !== 'waiting') {
            setRates(old => [...old, newMessage.messageCount - prev.messageCount])
          }
          return {
            ...newMessage,
            ...(prev ? {} : {})
          }
        }
      );
    }
  }, [lastMessage]);

  let connectionStatus = {
    [ReadyState.CONNECTING]: 'Connecting',
    [ReadyState.OPEN]: 'Open',
    [ReadyState.CLOSING]: 'Closing',
    [ReadyState.CLOSED]: 'Closed',
    [ReadyState.UNINSTANTIATED]: 'Uninstantiated',
  }[readyState];

  return (
    <Container>
      {/* {description} */}
      <textarea
        value={socketUrl}
        readOnly={true}
        onClick={() => {navigator.clipboard.writeText(socketUrl)}}
        name="text" 
        ninput='this.style.height = "";this.style.height = this.scrollHeight + "px"'
      ></textarea>
      <br/>
      <br/>
      <span>The WebSocket is currently {connectionStatus}</span>
      <br/>
      <br/>
      {content !== undefined ? jsonCodeBlock(content) : null}
      <br/>
      <br/>
      <button onClick={() => setRates([])}>Clear graph</button>
      <br/>
      <br/>
      Incoming messages: {incomingCount}
      <br/>
      <br/>
      <Line
        updateMode={'none'}
        data={{
          labels: Array(60).fill().map((v, i) => (60 - i) * -1),
          datasets: [
            {
              label: 'messageCountDelta',
              data: rates.slice(-60),
              borderColor: '#40daf5',
              backgroundColor: '#40daf5',
            }
          ]
        }}
      />
    </Container>
  )
}

export default MonitorApp
