import { useState } from 'react'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import * as d3 from 'd3'
import { pmt } from 'financial'
import { Chart } from 'react-chartjs-2'
import _ from 'lodash'


let initialState = {
  monthlyIncome: 3100,
  monthlyDebt: 500,
  // downPayment: 50000,
  percentDown: 0.20,
  loanType: 'Fixed',
  nPayments: 360,
  interestRate: 0.065,
  minDown: 20000,
  maxDown: 80000,
  dollarInterval: 1000,
  minRate: 0.01,
  maxRate: 0.09,
  rateInterval: 0.005
}


let IncomeHousing = () => {
  let [state, setState] = useState(initialState)

  let [selected, setSelected] = useState([null, null])
  let [isHover, setIsHover] = useState(true)

  let downPayments = Array(
    Math.floor((state.maxDown - state.minDown) / state.dollarInterval)
  ).fill().map(
    (i, x) => (x * state.dollarInterval) + state.minDown
  )

  let principles = downPayments.map(
    (downPayment, x) => (downPayment * (1 / state.percentDown)) - downPayment
  )

  let interestRates = Array(
    Math.ceil((state.maxRate - state.minRate) / state.rateInterval)
  ).fill().map(
    (i, x) => (x * state.rateInterval) + state.minRate
  )

  let housingIncomeColors = d3.scaleQuantize()
    .domain([0.28, 0.3, 0.4])
    .range([
      '#44ce1b', '#bbdb44', '#fcdb03', '#e51f1f'
    ]);

  let colors = d3.scaleQuantize()
    .domain([0.36, 0.38, 0.5])
    .range([
      '#44ce1b', '#bbdb44', '#fcdb03', '#e51f1f'
    ]);

  let paymentMatrix = downPayments.map(
    (down, i) => interestRates.map(
      (interestRate, j) => pmt(interestRate / 12, state.nPayments, -(down * (1 / state.percentDown)))
    )
  )

  let housingIncomeMatrix = paymentMatrix.map(
    (x, i) => x.map((y, j) => y / state.monthlyIncome)
  )

  let debtIncomeMatrix = paymentMatrix.map(
    (x, i) => x.map((y, j) => (y + state.monthlyDebt) / state.monthlyIncome)
  )

  let getInterestPayback = ([i, j]) => {
    let ret = []
    let principle = principles[i]
    let interestRate = interestRates[j]
    let payment = paymentMatrix[i][j]
    for (let n = 1; n < state.nPayments + 1; n++) {
      ret.push({
        paymentNumber: n,
        loanBalance: payment * (state.nPayments - n),
        paid: payment * n,
        interestPortion: (payment * (state.nPayments - n)) * (interestRate / 12)
      })
    }
    return ret
  }
  return (<>
    <Row>
      <Col>
        <h3>Plot affordability</h3>
        Monthly Income:&nbsp;$<input
          type="number"
          step="50"
          value={state.monthlyIncome}
          onClick={(e) => e.target.select()}
          onChange={(e) => setState(prev => ({ ...prev, monthlyIncome: Number(e.target.value) }))}
        />
        <br />
        <br />
        Monthly Debt:&nbsp;$<input
          type="number"
          step="50"
          value={state.monthlyDebt}
          onClick={(e) => e.target.select()}
          onChange={(e) => setState(prev => ({ ...prev, monthlyDebt: Number(e.target.value) }))}
        />
        <br />
        <br />
        {/* Down Payment:&nbsp;<input
          type="number"
          value={state.downPayment}
          onClick={(e) => e.target.select()}
          onChange={(e) => setState(prev => ({...prev, downPayment: Number(e.target.value)}))}
        />$
        <br/>
        <br/> */}
        Percent Down:&nbsp;<input
          type="number"
          value={state.percentDown * 100}
          onClick={(e) => e.target.select()}
          onChange={(e) => setState(prev => ({ ...prev, percentDown: Number(e.target.value) / 100 }))}
        />%
        <br />
        <br />
        Mortgage/Loan duration:&nbsp;
        <input
          type="number"
          value={(state.nPayments / 12).toFixed(2)}
          onClick={(e) => e.target.select()}
          onChange={(e) => setState(prev => ({ ...prev, nPayments: Number(e.target.value) * 12 }))}
        /> Years
        <br />
        <br />
        Number of Payments:&nbsp;
        <input
          step="10"
          type="number"
          value={state.nPayments}
          onClick={(e) => e.target.select()}
          onChange={(e) => setState(prev => ({ ...prev, nPayments: Number(e.target.value) }))}
        />
        <br />
        <br />
        Min Down:&nbsp;$<input
          type="number"
          step="1000"
          value={state.minDown}
          onClick={(e) => e.target.select()}
          onChange={(e) => setState(prev => ({ ...prev, minDown: Number(e.target.value) }))}
        />
        <br />
        <br />
        Max Down:&nbsp;$<input
          type="number"
          step="1000"
          value={state.maxDown}
          onClick={(e) => e.target.select()}
          onChange={(e) => setState(prev => ({ ...prev, maxDown: Number(e.target.value) }))}
        />
        <br />
        <br />
        Dollar Interval:&nbsp;$<input
          type="number"
          step="50"
          value={state.dollarInterval}
          onClick={(e) => e.target.select()}
          onChange={(e) => setState(prev => ({ ...prev, dollarInterval: Number(e.target.value) }))}
        />
        <br />
        <br />
        Min Rate:&nbsp;<input
          type="number"
          value={state.minRate * 100}
          onClick={(e) => e.target.select()}
          onChange={(e) => setState(prev => ({ ...prev, minRate: Number(e.target.value) / 100 }))}
        />%
        <br />
        <br />
        Max Rate:&nbsp;<input
          type="number"
          value={state.maxRate * 100}
          onClick={(e) => e.target.select()}
          onChange={(e) => setState(prev => ({ ...prev, maxRate: Number(e.target.value) / 100 }))}
        />%
        <br />
        <br />
        Rate Interval:&nbsp;<input
          type="number"
          step=".1"
          value={state.rateInterval * 100}
          onClick={(e) => e.target.select()}
          onChange={(e) => setState(prev => ({ ...prev, rateInterval: Number(e.target.value) / 100 }))}
        />%
        <br />
        <br />
        Principle: $ {_.isNumber(selected[0]) ? ((1 / state.percentDown) * downPayments[selected[0]]).toLocaleString() : null}
        <br />
        Down Payment: $ {_.isNumber(selected[0]) ? downPayments[selected[0]].toLocaleString() : null}
        <br />
        Interest Rate: {_.isNumber(selected[0]) ? (interestRates[selected[1]] * 100).toFixed(2) : null} %
        <br />
        Monthly Payment: $ {_.isNumber(selected[0]) ? (paymentMatrix[selected[0]][selected[1]]).toLocaleString() : null}
        <br />
        Housing-to-Income Ratio: {_.isNumber(selected[0]) ? (housingIncomeMatrix[selected[0]][selected[1]] * 100).toFixed(2) : null} %
        <br />
        Debt-to-Income Ratio: {_.isNumber(selected[0]) ? (debtIncomeMatrix[selected[0]][selected[1]] * 100).toFixed(2) : null} %
        <br />
        Total Loan Cost: $ {_.isNumber(selected[0]) ? (paymentMatrix[selected[0]][selected[1]] * state.nPayments).toLocaleString() : null}
        <br />
        <button onClick={() => {
          setState(initialState)
          setIsHover(true)
          setSelected([null, null])
        }}>Reset</button>
      </Col>
      <Col>
        <h3>Debt to Income</h3>
        <div className="grid">
          {downPayments.map((downPayment, i) => (<Row key={i}>
            {interestRates.map((interestRate, j) => (
              <div
                key={`${i},${j}`}
                className="grid-element"
                onClick={(e) => {
                  setIsHover(!isHover)
                  setSelected([i, j])
                }}
                onMouseEnter={(e) => {
                  if (isHover) setSelected([i, j])
                }}
                onMouseLeave={(e) => {
                  if (isHover) setSelected([null, null])
                }}
                style={{ backgroundColor: colors(debtIncomeMatrix[i][j]) }}
              >&nbsp;</div>
            ))}
          </Row>))}
        </div>
      </Col>
      <Col>
        <h3>Housing to Income</h3>
        <div className="grid">
          {downPayments.map((downPayment, i) => (<Row key={i}>
            {interestRates.map((interestRate, j) => (
              <div
                key={`${i},${j}`}
                className="grid-element"
                onClick={(e) => {
                  setIsHover(!isHover)
                  setSelected([i, j])
                }}
                onMouseEnter={(e) => {
                  if (isHover) setSelected([i, j])
                }}
                onMouseLeave={(e) => {
                  if (isHover) setSelected([null, null])
                }}
                style={{ backgroundColor: housingIncomeColors(housingIncomeMatrix[i][j]) }}
              >&nbsp;</div>
            ))}
          </Row>))}
        </div>
      </Col>

    </Row>
    <Row>
      <Chart
        type='bar'
        options={{
          plugins: {
            title: {
              display: true,
              text: 'Amortization'
            }
          },
          scales: {
            x: {
              title: {
                display: true,
                text: 'Payment Number #'
              }
            },
            y: {
              type: 'linear',
              display: true,
              position: 'left',
              ticks: {
                // Include a dollar sign in the ticks
                callback: function(value, index, ticks) {
                    return '$' + value;
                }
            }
            },
            y1: {
              type: 'linear',
              display: true,
              position: 'right',
              ticks: {
                // Include a dollar sign in the ticks
                callback: function(value, index, ticks) {
                    return '$' + value.toLocaleString();
                }
            }
            }
          }
        }}
        data={{
          labels: Array(state.nPayments).fill().map((i, x) => x),
          datasets: [
            {
              label: 'Payments',
              data: _.isNumber(selected[0]) ? Array(state.nPayments).fill().map((i, x) => paymentMatrix[selected[0]][selected[1]]): null,
              borderColor: `rgba(0, 255, 115, 0.5)`,
              backgroundColor: `rgba(0, 255, 115, 0.5)`,
            },
            {
              label: 'Amount Spent',
              type: 'line',
              data: (
                _.isNumber(selected[0]) ? Array(state.nPayments).fill().map((n, x) => paymentMatrix[selected[0]][selected[1]] * x): null
              ),
              borderColor: `rgba(238, 255, 0, 0.5)`,
              backgroundColor: `rgba(238, 255, 0, 0.5)`,
              yAxisID: 'y1',
            },
            {
              label: 'Interest payback',
              type: 'line',
              data: (
                _.isNumber(selected[0]) ? _.map(getInterestPayback(selected), _.get('')): null
              ),
              borderColor: `rgba(0, 0, 255, 0.5)`,
              backgroundColor: `rgba(0, 0, 255, 0.5)`,
              // yAxisID: 'y',
            }
          ]
        }}
      />
    </Row>
  </>)
}


export default IncomeHousing

