import { jsonCodeBlock } from '../utils';
import * as rdd from 'react-device-detect';
import { useEffect, useState } from 'react';
import _ from 'lodash/fp'
import F from 'futil'


let getInfo = _.flow([
  _.pick(['osName', 'osVersion', 'getUA', 'mobileModel', 'mobileVendor', 'browserName', 'fullBrowserVersion']),
  F.renameProperty('getUA', 'userAgent'),
  _.pickBy((v) => v != 'none')
])

let getWindowState = () => ({
  zoom: Math.floor((( window.outerWidth - 10 ) / window.innerWidth) * 100),
  height: window.innerHeight,
  width: window.innerWidth,
  // fakeEmail: faker.internet.email()
})


let ShowClientInfo = () => {
  let [windowState, setWindowState] = useState(getWindowState());
  let [clientIp, setClientIp] = useState(null)

  // useEffect(() => {
  //   fetch(
  //     "http://localhost:8080/ident"
  //   ).then(
  //     (res) => res.json()
  //   ).then(
  //     (data) => setClientIp(data.clientIp)
  //   )
  // }, [clientIp])

  // setInterval(() => setWindowState(getWindowState()), 100)
  // , host: window.location.host

  return (
    <div id='client-info'>
      {jsonCodeBlock({
        window: windowState,
        clientInfo: {...getInfo(rdd), ip: clientIp}
      })}
    </div>
  )
}

export default ShowClientInfo