import { useEffect, useState } from 'react';
import { Line } from 'react-chartjs-2';
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import _ from 'lodash';

function* genFibs(n) {
  for (var i = 0, a = 0, b = 1; i < n; i++) {
    yield a;
    let t = a + b
    a = b
    b = t
  }
}

function* genCollatz(n) {
  while (n != 1) {
    yield n
    n = n % 2 == 0 ? n / 2 : n * 3 + 1
  }
}

function* genCauchy(n) {
  
}

let cauchy = [1, 2, 6, 4, 30, 12, 84, 24, 90, 20, 132, 24, 5460, 840, 360, 16, 1530, 180, 7980, 840, 13860, 440, 1656, 720, 81900, 6552, 216, 112, 3480, 240, 114576, 7392, 117810, 2380, 1260, 72, 3838380, 207480, 32760, 560, 568260, 27720, 238392, 55440, 869400, 2576, 236880]

let SequenceApp = () => {
  let [inputN, setInputN] = useState(32)
  let [outArray, setOutArray] = useState({fibonacci: [], collatz: []})
  let [logScaleBit, setLogScale] = useState(false)

  useEffect(() => {
    setOutArray({fibonacci: [], collatz: []})
    for (let j of genFibs(inputN)) {
      setOutArray(prev => ({...prev, fibonacci: [...prev.fibonacci, j]}))
    }
    for (let i = 1; i <= inputN; i++) {
      let tmp = [...genCollatz(i)]
      setOutArray(prev => ({...prev, collatz: [...prev.collatz, tmp.length]}))
      // setOutArray(old => [...old, tmp.length])
    }
    setOutArray(prev => ({...prev, cauchy: _.slice(cauchy, 0, inputN)}))
  }, [inputN])
  return (
    <Container>
      <Row>
        Integer (N):&nbsp;<input
          type='number'
          value={inputN}
          onChange={(event) => {
            setInputN(event.target.value ? parseInt(event.target.value) : 1)
            // handleFibsGo()
          }}
        />
        &nbsp;
        &nbsp;
        {/* <button onClick={handleFibsGo}>Go</button> */}
        &nbsp;
        &nbsp;
        <button onClick={() => setOutArray([])}>Clear</button>
        <br/>
        <br/>
        Log Scale:&nbsp;<input type="checkbox" onChange={() => setLogScale(~logScaleBit)}></input>
        <br/>
        <br/>
      </Row>
      <Row>
        <Line
          data={{
            labels: Array(inputN).fill().map((x, i) => i),
            datasets: [
              {
                label: 'Fibonacci',
                data: outArray.fibonacci,
                borderColor: '#40daf5',
                backgroundColor: '#40daf5',
                yAxisID: 'y',
              },
              {
                label: 'Collatz',
                data: outArray.collatz,
                borderColor: '#f5c440',
                backgroundColor: '#f5c440',
                yAxisID: 'y1',
              },
              // {
              //   label: 'Cauchy',
              //   data: outArray.cauchy,
              //   borderColor: '#0388fc',
              //   backgroundColor: '#0388fc',
              //   yAxisID: 'y2',
              // },
            ],
          }}
          options={{
            responsive: true,
            plugins: {
              legend: {
                position: 'top',
              },
              title: {
                display: true,
                text: 'Sequence',
              },
            },
            scales: {
              y: {
                type: logScaleBit ? 'logarithmic': 'linear',
                display: true,
                position: 'left'
              },
              y1: {
                type: logScaleBit ? 'logarithmic': 'linear',
                display: true,
                position: 'right'
              },
              y2: {
                type: logScaleBit ? 'logarithmic': 'linear',
                display: true,
                position: 'middle'
              }
            }
          }}
        />
      </Row>
    </Container>
  )
}

export default SequenceApp