import _ from 'lodash/fp';

export let jsonPretty = (value) => JSON.stringify(value, undefined, 2);

export let codeBlock = (value) => (
  <pre className='code'>
    {value}
  </pre>
)

export let jsonCodeBlock = _.flow(
  jsonPretty,
  codeBlock
)


export let crossCheck = (isBool) => (<>{isBool ? "✅" : "❌"}</>)
